.action-keyhole-wrapper {
  position: relative;
  overflow: hidden;
  z-index: 0;
}

.keyhole {
  position: fixed;
  inset: 0;
  pointer-events: none;
  background: rgba(255, 204, 100, 0.95); /* make visible */
  clip-path: polygon(0% 0%, 0% 100%, 25% 100%, 25% 25%, 75% 25%, 75% 75%, 25% 75%, 25% 100%, 100% 100%, 100% 0%);
  z-index: 10;
}


.arrow {
  position: absolute;
  top: 72.5vh;
  left: 50%;
  z-index: 11;
  animation: float 1s ease-in-out infinite alternate both;
}

.arrow svg {
  transform: rotate(90deg);
  stroke: #2d3436;
  width: 2rem;
  margin-left: -1rem;
  height: auto;
}

@keyframes float {
  from {
    transform: translateY(-50%);
  }
  to {
    transform: translateY(50%);
  }
}
